export default{
  name: 'Item',
  props: {
    txt: {
      type: String,
      default: ''
    },
    page: {
      type: String,
      default: ''
    },
    sel: {
      type: String,
      default: ''
    }
  },
  computed: {
    bol: function () {
      if (this.sel === this.page) {
        return true
      }
      return false
    }
  },
  methods: {
    changePage: function () {
      // 点击跳转对应的页面
      this.$router.push('/' + this.page)
      this.$emit('change', this.page)
    }
  }
}



