
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import data from '../../data/data'
const baseList = [
{
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vvsr72j20p00gogo2.jpg',
title: '送你一朵'
},
{
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw1k2wj20p00goq7n.jpg',
title: '送你一辆车'
}
]
const urlList = baseList.map((item, index) => ({
url: 'http://m.baidu.com',
img: item.img,
fallbackImg: item.fallbackImg,
title: `(可点击)${item.title}`
}))

export default {
name: 'Application',
components: {
Swiper,
SwiperItem,
GroupTitle,
XButton,
Grid,
GridItem,
Badge,
Divider,
XSwitch,
Group,
Cell,
Alert
},
data () {
return {
msg: 'Welcome to Your Home.js App',
imageList: urlList,
imageIndex: 0,
isLoadingData: true,
slideShowList: [],
quickMenuList: [],
swiperItemIndex: 1,
slideShowIndex: 0,
commonsApplicationList: [],
newTip: false
}
},
mounted () {


this.quickMenuList = data.menuCateories
this.commonsApplicationList = data.menuCateories.commonsApplication[0].menus
if (this.commonsApplicationList) {
this.commonsApplicationList.push({

'menuResource': 'homeManage',
'menuName': '添加'
})
}
},
methods: {
onSwiperItemIndexChange (index) {

},
onIndexChange (index) {
this.imageIndex = index
},
slideShowOnIndexChange (index) {
this.slideShowIndex = index
},
goTarget (item) {
if (item.menuName === '问与答') {
window.location.href = item.menuResource
} else {
let selectItem = item.menuResource.split('/#/')[1]
if (selectItem) {
this.$router.push('/' + selectItem)
} else {
this.$router.push('/' + item.menuResource)
}
}
}
}
}



